:root {
  --primary-color: rgb(69, 69, 69);
  --secondary-color: rgb(228, 228, 228);
  --tertiary-color: rgb(176, 213, 255);
  --button-color: rgba(228, 228, 228, 0.7);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: cursive;
  background-color: var(--primary-color);
  margin: 0;
  padding: 0;
  width: 100%;
}

header {
  margin: auto;
  padding: 55px 90px 250px;
  width: 100%;
  background-image: url(./images/photo-big-screen.jpg);
  background-size: cover;
  text-align: center;
  border-bottom: 2px solid var(--secondary-color);
}

.header-box {
  background-color: rgba(69, 69, 69, 0.5);
  margin: 40px;
  padding: 15px;
  border-radius: 8px;
  overflow: hidden;
}

/* header title */
.header-title {
  font-size: 100px;
  color: var(--secondary-color);
}

/* header nav */
header nav {
  margin: 7px 0;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  list-style: none;
}

header nav ul li {
  font-weight: lighter;
  font-size: 25px;
  color: white;
  padding: 10px;
}

/* about me */
.about-me,
.work,
.contact-me {
  display: flex;
  justify-content: center;
  margin: 25px;
  line-height: 1.5;
  color: var(--secondary-color);
}

.about-me-title,
.work-title,
.contact-me-title {
  width: 20%;
  padding-right: 20px;
  text-align: end;
  font-size: 40px;
  font-weight: lighter;
}

.about-me-text,
.contact-me-text {
  width: 80%;
  padding-left: 30px;
  border-left: 2px solid var(--secondary-color);
  /* border: 1px red solid; */
}

.about-me-text div {
  background-color: rgba(0, 0, 0, 0.5);
  position: relative;
  width: 100%;
}

/* work */
.work-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 80%;
  border-left: 1px solid var(--secondary-color);
  /* border: 1px red solid; */
}

.work {
  display: flex;
  justify-content: space-between;
}

.work-link {
  width: 100%;
  margin: 5%;
  overflow: hidden;
  border: 1px solid var(--secondary-color);
}

.work-link a {
  text-decoration: none;
}

.hero-project {
  width: 100%;
  margin-top: 0;
}

.work-text {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: var(--secondary-color);
  width: 100%;
  line-height: 1.5;
  padding: 5px 10px;
  color: var(--primary-color);
}

.work-text h4 {
  font-size: 55px;
}

.work-text p {
  font-size: 35px;
}

.work-links div {
  position: relative;
}

.work-links div img {
  width: 100%;
  height: 100%;
  /* border: 1px red solid; */
}

/* contact me section */

.contact-me-title {
  font-size: 32px;
}

.contact-me-title h3 {
  color: var(--secondary-color);
}

.contact-me-text {
  display: flex;
  justify-content: space-between;
  padding: 25px;
  overflow: hidden;
}

.contact-me-text a {
  text-decoration: none;
  font-size: 1rem;
  color: var(--secondary-color);
}

button {
  display: inline-block;
  border: none;
  border-radius: 8px;
  padding: 1rem 2rem;
  margin: 0;
  text-decoration: none;
  background: var(--button-color);
  color: var(--primary-color);
  font-family: cursive;
  font-size: 2rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

button:hover,
button:focus {
  background: #ececec;
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  transform: scale(0.99);
}

footer {
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.5);
}
